import Vue from 'vue'
import VueRouter from 'vue-router'
import HomeView from '../views/HomeView.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'home',
    component: HomeView
  },
  {
    path: '/about',
    name: 'about',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "about" */ '../views/AboutView.vue')
  },
  {
    path: '/element',
    name: 'element',
    component: () => import( '../views/Element.vue')
  },
  {
    path: '/staricon',
    name: 'staricon',
    component: () => import( '../views/Staricon.vue')
  },
  {
    path: '/userlist',
    name: 'userlist',
    component: () => import( '../views/Userlist.vue')
  },
  {
    path: '/messages/list',
    name: 'messagelist',
    component: () => import( '../views/MessageView/MessageView.vue')
  },
  {
    path: '/messages/admin',
    name: 'backendadmin',
    component: () => import( '../views/MessageView/BackendView.vue')
  },
  {
    path: '/kbsview',
    name: 'kbsview',
    component: () => import( '../views/ProgremView/KbsView.vue')
  },
  {
    path: '/ysbzview',
    name: 'ysbzview',
    component: () => import( '../views/ProgremView/YsbzView.vue')
  },
  {
    path: '/login',
    name: 'login',
    component: () => import( '../views/LoginView.vue')
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
